}

/* Create four equal columns that sits next to each other */
.column {
  -webkit-flex: 20% 1;
          flex: 20% 1;
  /* max-width: 33%; */
  padding: 0 4px;
}

.column img {
  /* margin-top: 8px; */
  vertical-align: middle;
  width: 100%;
  /* height: 500px; */
  /* object-fit: contain; */
  display: block;
}

.image--container {
  position: relative;
  /* border: 1px solid blue; */
  transition: 0.5s ease;
}
.overlay50 {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: 0.5s ease;
  background-color: #111;
}

.image--container:hover .overlay50 {
  opacity: 0.6;
}

.scaleImage {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
}

img {
  transition: 0.5s ease-in-out;
  margin-bottom: 8px;
}

.image--container:hover,
.overlay50:hover img {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}
.overlay50:hover {
  margin-bottom: 0px;
}

/* desktop behaviour */
.likesContainer {
  display: none;
  position: absolute;
  top: 78%;
  left: 5%;
}

.image--container:hover .likesContainer {
  display: block;
}

